@media print {
  *,
  *::before,
  *::after {
    background: white !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  @page {
    size: A4;
    margin: 20mm 15mm 25mm 15mm;
  }
  @page :first {
    margin-top: 15mm;
  }
  nav,
  header,
  footer,
  .print\:hidden,
  [class*="NewsTicker"],
  [class*="ChatWidget"],
  [class*="CookieBanner"],
  [class*="BackToTop"],
  [class*="CustomCursor"],
  [class*="ScrollProgress"],
  [class*="ThemeToggle"],
  button:not([data-print-show]),
  .skip-link,
  [aria-label=""],
  [data-print-hide] {
    display: none !important;
  }
  body {
    font-family: 'Sarabun', 'Tahoma', sans-serif !important;
    font-size: 11pt;
    line-height: 1.8;
  }
  main {
    padding: 0 !important;
    margin: 0 !important;
  }
  [style*="var(--primary-color)"] {
    background: white !important;
    color: black !important;
    border-bottom: 2pt solid #000 !important;
    padding: 8mm 0 6mm !important;
  }
  h1 {
    font-size: 18pt;
    font-weight: 800;
    margin-bottom: 6mm;
  }
  h2 {
    font-size: 14pt;
    font-weight: 700;
    margin-top: 8mm;
    margin-bottom: 4mm;
  }
  h3 {
    font-size: 12pt;
    font-weight: 600;
    margin-top: 6mm;
    margin-bottom: 3mm;
  }
  p {
    margin-bottom: 4mm;
    orphans: 3;
    widows: 3;
  }
  .legal-content {
    max-width: 100% !important;
  }
  .legal-content h1,
  .legal-content h2,
  .legal-content h3 {
    page-break-after: avoid;
  }
  .legal-content table {
    width: 100%;
    border-collapse: collapse;
    page-break-inside: avoid;
  }
  .legal-content th,
  .legal-content td {
    border: 0.5pt solid #333;
    padding: 3mm 4mm;
    font-size: 10pt;
  }
  article {
    border: none !important;
    border-radius: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    box-shadow: none !important;
  }
  img {
    max-width: 100% !important;
    page-break-inside: avoid;
    print-color-adjust: exact;
  }
  a[href^="http"]::after {
    content: " (" attr(href) ")";
    font-size: 8pt;
    color: #555 !important;
    font-style: italic;
    word-break: break-all;
  }
  a[href^="/"]:not([href^="//"])::after {
    content: "";
  }
  nav[aria-label="Breadcrumb"] {
    display: block !important;
    font-size: 8pt;
    color: #555 !important;
    margin-bottom: 4mm;
  }
  body::after {
    content: "";
    display: block;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    page-break-after: avoid;
    break-after: avoid;
  }
  figure,
  table,
  blockquote {
    page-break-inside: avoid;
    break-inside: avoid;
  }
  main>div:last-child {
    border-top: 0.5pt solid #ccc !important;
    padding-top: 4mm !important;
    margin-top: 8mm !important;
    font-size: 8pt !important;
    color: #555 !important;
  }
}
